import { useState, useEffect } from 'react';
import { useEffectUpdate } from '../../hooks';
import { useParams } from 'react-router-dom';
import {
	Seo,
	JobCard,
	Breadcumb,
	Container,
	Filter,
	LayoutContainer,
	FluidContainer,
	Title,
} from '../../components';
import {
	MainContainer,
	JobContainer,
	FilterBtn,
	PaginateComponent,
} from './JobList.elements';
import axios from '../../axios';
import ReactPaginate from 'react-paginate';
import Skeleton from 'react-loading-skeleton';

const JobList = (props) => {
	let { categoryName } = useParams();
	const [filterData, setfilterData] = useState({
		company: '',
		location: '',
		jobtitle: '',
		salary: 0,
		experience: '',
		page: 1,
		search: props.location.state ? props.location.state.searchValue : '',
	});
	const [isFilterOpen, setisFilterOpen] = useState(false);
	const [isLoading, setIsLoading] = useState(true);
	const [data, setData] = useState([]);
	const [numPages, setNumPages] = useState(-1);
	const [is404, setIs404] = useState(false);
	const title = filterData.search !== '' ? 'search results' : `${categoryName}`;

	const [seoData, setSeoData] = useState({
		title: '',
		description: '',
	});

	const breadData = [
		{ name: 'home', link: '/' },
		{ name: title, link: `/category/${categoryName}` },
	];
	if (categoryName === 'search') {
		categoryName = 'jobs';
	}
	useEffectUpdate(() => {
		onFilterClear();
	}, [categoryName]);

	useEffect(() => {
		async function fetchData() {
			if (is404 === true) {
				setIs404(false);
			}
			if (isLoading === false) {
				setIsLoading(true);
			}
			let apiUrl = `joblist/${categoryName}/?location=${filterData.location}&company=${filterData.company}&jobtitle=${filterData.jobtitle}&salary=${filterData.salary}&experience=${filterData.experience}&page=${filterData.page}`;
			if (filterData.search !== '') {
				apiUrl = apiUrl + `&search=${filterData.search}`;
			}
			try {
				const response = await axios.get(apiUrl);
				setData(response.data.results);
				setIsLoading(false);
				setNumPages(Math.ceil(response.data.count / response.data.page_size));
				setSeoData({
					title: response.data.title,
					description: response.data.description,
				});
			} catch (error) {
				setIsLoading(false);
				setIs404(true);
			}
		}
		fetchData();
	}, [filterData]);

	const JobCards = () => {
		return (
			<>
				{data.map((item, index) => (
					<JobCard {...item} key={index} />
				))}
			</>
		);
	};
	const toggleFilterClick = () => {
		setisFilterOpen(!isFilterOpen);
	};
	const handlePageClick = (data) => {
		setfilterData({
			...filterData,
			page: data.selected + 1,
		});
	};
	const onFilterSubmit = (name, value) => {
		setfilterData({ ...filterData, [name]: value, page: 1 });
	};
	const onFilterClear = () => {
		setfilterData({
			...filterData,
			company: '',
			location: '',
			jobtitle: '',
			salary: 0,
			experience: '',
			page: 1,
		});
	};

	return (
		<LayoutContainer is404={is404}>
			<Seo title={seoData.title} description={seoData.description} />
			<FluidContainer>
				<Container>
					<Breadcumb breadData={breadData} />
					<Title>{isLoading ? <Skeleton /> : seoData.title}</Title>
					<MainContainer>
						{!isLoading && (
							<FilterBtn onClick={toggleFilterClick}>
								{isFilterOpen ? 'Hide Filters' : 'Show Filters'}
							</FilterBtn>
						)}

						<Filter
							onFilterSubmit={onFilterSubmit}
							isFilterOpen={isFilterOpen}
							filterData={filterData}
							onFilterClear={onFilterClear}
							isLoading={isLoading}
							filterFor={categoryName}
						/>
						<JobContainer>
							{isLoading ? (
								[...Array(10)].map((e, i) => <JobCard key={i} />)
							) : (
								<JobCards />
							)}
							<PaginateComponent>
								<ReactPaginate
									previousLabel={`prev`}
									nextLabel={'next'}
									breakLabel={'...'}
									breakClassName={'break-me'}
									pageCount={numPages}
									marginPagesDisplayed={3}
									pageRangeDisplayed={2}
									onPageChange={handlePageClick}
									containerClassName={'pagination'}
									subContainerClassName={'pages pagination'}
									activeClassName={'active'}
									disabledClassName={'disabled'}
									forcePage={filterData.page - 1}
								/>
							</PaginateComponent>
						</JobContainer>
					</MainContainer>
				</Container>
			</FluidContainer>
		</LayoutContainer>
	);
};

export default JobList;
